<template>
	<div class="hotBodyBox">
		<section class="hotBodyHeader">
			<span class="hoyBodyTit"></span>
			<div class="hotBodyNav">
				<span v-for="(item,index) in tabs" :class="{active:item.id == num}" @click="itemInfo(item.id)">{{item.name}}</span>				
			</div>
		</section>
		<section class="hotBodyCentent">
			<ul>
				<li>
					<img src="https://ss0.bdstatic.com/94oJfD_bAAcT8t7mm9GUKT-xh_/timg?image&quality=100&size=b4000_4000&sec=1511436551&di=e1620ced6e488c5aec4d4c5ac16bd20e&src=http://img.zcool.cn/community/01dc3558d0c8b0a801219c77d1ac82.jpg@900w_1l_2o_100sh.jpg" alt="">
					<p></p>
					<div>
						<span></span>
						<span></span>
					</div>
				</li>
				<li>
					<img src="https://ss0.bdstatic.com/94oJfD_bAAcT8t7mm9GUKT-xh_/timg?image&quality=100&size=b4000_4000&sec=1511436551&di=e1620ced6e488c5aec4d4c5ac16bd20e&src=http://img.zcool.cn/community/01dc3558d0c8b0a801219c77d1ac82.jpg@900w_1l_2o_100sh.jpg" alt="">
					<p></p>
					<div>
						<span></span>
						<span></span>
					</div>
				</li>
				<li>
					<img src="https://ss0.bdstatic.com/94oJfD_bAAcT8t7mm9GUKT-xh_/timg?image&quality=100&size=b4000_4000&sec=1511436551&di=e1620ced6e488c5aec4d4c5ac16bd20e&src=http://img.zcool.cn/community/01dc3558d0c8b0a801219c77d1ac82.jpg@900w_1l_2o_100sh.jpg" alt="">
					<p></p>
					<div>
						<span></span>
						<span></span>
					</div>
				</li>
				<li>
					<img src="https://ss0.bdstatic.com/94oJfD_bAAcT8t7mm9GUKT-xh_/timg?image&quality=100&size=b4000_4000&sec=1511436551&di=e1620ced6e488c5aec4d4c5ac16bd20e&src=http://img.zcool.cn/community/01dc3558d0c8b0a801219c77d1ac82.jpg@900w_1l_2o_100sh.jpg" alt="">
					<p></p>
					<div>
						<span></span>
						<span></span>
					</div>
				</li>				
			</ul>
		</section>
	</div>
</template>

<script type="text/ecmascript-6">

export default {
	data(){
		return{
			tabs: [],
        	tabContents: '',
        	num: '',
			getTypeList: path+'/index/company/company-type',
			getItemInfo: path+'/index/index/hot-company'
		}
	},
	created: function(){
		var _this = this
			this.$http.get(_this.getTypeList,{emulateJSON:true}).then(function(response){
				var data = response.data
				_this.num = data[0].id
				for (var i = 0; i < data.length; i++) {
					_this.tabs.push({id:data[i].id,name:data[i].name})
				}
				_this.itemInfo(_this.num)
			},function(response){
                alert('')
            })
	},
    methods:{
		itemInfo: function (id) {
			console.log(id)
			var _this = this
			_this.num = id
			this.$http.get(_this.getItemInfo,{qualification_id:id},{emulateJSON:true}).then(function(response){
				var data = response.data
				
			},function(response){
                alert('')
            })
		}
    }
  }
</script>

<style media="screen">
	.hoyBodyTit{
		font-size: 18px;
		color: #4a4a4a;
	}
	.hotBodyHeader{
		display: flex;
		height: 58px;
		width: 100%;
		justify-content: space-between;
		align-items: center;
	}
	.hotBodyNav span{
		padding: 0 5px;
		margin: 0 5px;
		color: #898989;
		border-bottom: 1px solid transition; 
	}
	.hotBodyNav span.active{
		color: #337ffb;
		border-bottom: 1px solid #337ffb;
	}
	.hotBodyCentent{
		display: flex;
		width: 100%;
		height: 198px;
		justify-content: center;
		align-items: center;
		border: 1px solid #efefef;
	}
	.hotBodyCentent ul{
		width: 100%;
		background: #ffffff;
	}
	.hotBodyCentent ul li{
		float: left;
		display: flex;
		width: 301px;
		height: 198px;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		border-left: 1px solid #f0f0f0;
		background: #ffffff;
	}
	.hotBodyCentent ul li img{
		width: 88px;
		height: 88px;
		border-radius: 100%;
	}
	.hotBodyCentent ul li p{
		padding-top: 18px;
		padding-bottom: 10px;
		font-size: 14px;
		color: #0b0b0b;
	}
	.hotBodyCentent ul li div span{
		padding: 0 10px;
		line-height: 24px;
		font-size: 12px;
		color: #ccc;
		border: 1px solid #e1e1e1;
	}
</style>



